<template>
  <div class="security">
    <van-nav-bar title="安全中心" left-text="返回" @click-left="goBack" />
    <div class="v-cell">
      <van-cell-group>
        <van-cell class="v-cell-box" title="修改密码" is-link @click="changePass" />
        <van-cell class="v-cell-box" title="注销账号" is-link @click="destroy" />
      </van-cell-group>
    </div>
    <div class="btnBox">
      <van-button class="noLogin-btn" :round="true" :block="true" type="danger" @click="noLogin">退出登录</van-button>
    </div>
    <van-popup v-model="isShow" round position="bottom" :style="{ height: '26%' }">
      <div class="pass-box">
        <van-form @submit="submitPass">
          <van-field class="inp" v-model="passData.oldPass" label="旧密码" placeholder="旧密码" />
          <van-field
            class="inp"
            v-model="passData.newPass"
            type="text"
            label="新密码"
            placeholder="新密码"
          />
          <div class="btn-box">
            <van-button round block type="info" native-type="submit" @submit="submitPass">确认修改</van-button>
          </div>
        </van-form>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: false,
      passData: {
        oldPass: "",
        newPass: ""
      },
      passReg: {
        reg: /^\w{6,16}$/,
        msg: "密码格式不正确"
      }
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    // 打开修改密码弹窗
    changePass() {
      this.isShow = true;
    },
    // 修改密码
    submitPass() {
      if (!this.passReg.reg.test(this.passData.newPass)) {
        this.$toast(this.passReg.msg);
        return;
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      let tkStr = localStorage.getItem("__Tk");

      this.axios({
        method: "POST",
        url: "/updatePassword",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          password: this.passData.newPass,
          oldPassword: this.passData.oldPass
        }
      })
        .then(result => {
          this.$toast.clear();
          //关闭修改密码弹出框
          this.isShow = false;
          if (result.data.code == "E001") {
            this.$toast(result.data.msg);
            localStorage.removeItem("__Tk");
            this.$router.push({ name: "Login" });
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    // 销号
    destroy() {
      this.$dialog
        .confirm({
          title: "是否注销账号",
          message: "此操作不可逆,注销后数据无法恢复"
        })
        .then(() => {
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0
          });
          let tkStr = localStorage.getItem("__Tk");

          this.axios({
            method: "post",
            url: "/destroyAccount",
            data: {
              appkey: this.appkey,
              tokenString: tkStr
            }
          })
            .then(result => {
              this.$toast.clear();
              if (result.data.code == "G001") {
                //清除token
                localStorage.removeItem("__Tk");
                this.$router.push({ name: "Login" });
              } else {
                this.$toast(result.data.msg);
              }
            })
            .catch(err => {
              this.$toast.clear();
            });
        })
        .catch(err => {});
    },
    // 退出登录
    noLogin(){
        this.$toast("已登出")
      localStorage.removeItem("__Tk");
      this.$router.push({ name: "Login" });
    }
  }
};
</script>

<style lang="less" scoped>
.security {
  /deep/ .van-nav-bar__text {
    color: steelblue;
    border-left: 1px dashed #eee;
    border-top: 1px dashed #eee;
    border-radius: 13px;
    padding: 2px 8px;
    box-shadow: 2px 2px 2px #ddd;
    line-height: 25px;
  }
  /deep/ .van-button--info {
    background-image: linear-gradient(to right, steelblue, steelblue);
    border: none;
  }

  .v-cell {
    margin-top: 10px;
    padding: 10px;
    background-color: #fff;
    border-radius: 10px;
    .v-cell-box {
      margin: 10px 0;
      box-shadow: 1px 1px 2px 1px #ddd;
      border-radius: 20px;
      color: steelblue;
    }
  }
  .btnBox {
    padding: 0 10px;
    .noLogin-btn {
      background-image: linear-gradient(to right bottom, lightblue, steelblue);
      border: none;
    }
  }
  .pass-box {
    padding: 30px 10px 0;
    .inp {
      border-bottom: 1px solid #eee;
      margin-bottom: 10px;
    }
  }
}
</style>